<style scoped lang="less">
.login {
  width: 100%;
  height: 100%;
  /*background: #2c3e50;*/
  position: relative;
  .login-info {
    width: 500px;
    height: 300px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 50px;
    .login-user {
      width: 210px;
      margin-top: 20px;
    }
    span {
      display: inline-block;
      font-size: 18px;
      cursor: pointer;
      color: #2c3e50;
      margin: 0 40px;
    }
  }
}
</style>
<template>
  <div class="login">
      <div class="login-info">
        <el-input class="login-user" v-model="user.userName" placeholder="请输入账号" :style="{'border-radius': '10px'}"></el-input>
        <el-input class="login-user" type="password" v-model="user.userpwd" placeholder="请输入密码" @keyup.enter.native="submit()"></el-input>
        <br>
        <br>
        <br>
        <span @click="toIndex">去首页</span>
        <span @click="submit()">登录</span>
      </div>
  </div>
</template>
<script>
  export default {
    name: 'login',
    data(){
        return {
            user: {
                userName: '',
                userpwd: ''
            }
        }
    },
    methods: {
      submit(){
          var userName = this.user.userName;
          if(userName == 'hospital'){
            this.$router.push({path: '/hospitalMenu'});
          }else if(userName == 'expert'){
            this.$router.push({path: '/expertMenu'});
          }else if(userName == 'allocating'){
            this.$router.push({path: '/allocatingMenu'});
          }

          else if(userName == 'manages'){
              /*超级管理员*/
            this.$router.push({path: '/manage/superManage/userManage'});
          }else if(userName == 'manage'){
              /*普通管理员*/
            this.$router.push({path: '/manage/ordinaryManage/hospitalManage'});
          }

          else if(userName == 'outlay'){
            this.$router.push({path: '/outlayMenu'});
          }else if(userName == 'release'){
            this.$router.push({path: '/releaseMenu'});
          }else {
            alert("用户名或密码错误")
          }
      },
      toIndex(){
          this.$router.push({path: '/'});
      }
    }
  }
</script>



